import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AddLookupService {

  private addLookupMatDrawer = new BehaviorSubject<any>(null);
  addlookupmatdrawercast = this.addLookupMatDrawer.asObservable();

  private addLookupEntryRefresh = new BehaviorSubject<any>(false);
  addlookupentryrefreshcast = this.addLookupEntryRefresh.asObservable();

  private addLookupOpenMatDrawer = new BehaviorSubject<any>(false);
  addlookupopenmatdrawercast = this.addLookupOpenMatDrawer.asObservable();

  constructor() { }

  getAddLookupMatDrawer(){
    return this.addLookupMatDrawer.value
  }

  setAddLookupMatDrawer(value){
    this.addLookupMatDrawer.next(value);
  }

  getAddLookupEntryRefresh(){
    return this.addLookupEntryRefresh.value
  }

  setAddLookupEntryRefresh(value){
    this.addLookupEntryRefresh.next(value);
  }

  getAddLookupOpenMatDrawer(){
    return this.addLookupOpenMatDrawer.value
  }

  setAddLookupOpenMatDrawer(value){
    this.addLookupOpenMatDrawer.next(value);
  }


}
