import { Component, OnInit } from '@angular/core';

import { LookupService } from 'src/app/api/controllers/Lookup';

@Component({
  selector: 'app-visitor-report',
  templateUrl: './visitor-report.component.html',
  styleUrls: ['./visitor-report.component.scss']
})
export class VisitorReportComponent implements OnInit {

  reportDataList:any;

  isDataLoaded:boolean = false;

  constructor(
    private lookupService: LookupService
  ) { }

  getReportName(name) {
    return name.replace(/\s+/g, '-').toLowerCase();
  }

  ngOnInit() {

    let details = {
      LookupTypeId: 87, //lookuptypeid for report
      MenuName: 'VisitorManagement'
    }

    this.lookupService.getLookupValuesByApartmentIdLookupTypeIdMenuName(details).subscribe((res:any) => {

      this.reportDataList = res;
      this.isDataLoaded = true;

    })


  }

}
